@import '../../styles/variables';
@import '../../styles/animation';

.home {
  margin-top: $nav-height;
  display: flex;
  flex-direction: column;
  transition: margin-left 0.5s;

  &-push {
    margin-left: $sidebar-width;
  }

  &__globe {
    margin: 2rem auto 1rem;
    font-size: 6rem;
    color: gray;
    animation: rotating 20s linear infinite;
    z-index: 0;
  }

  &__title {
    text-align: center;
    &-red {
      color: $clr-red;
    }
    &-blue {
      color: $clr-blue;
    }
    &-green {
      color: $clr-green;
    }
    &-purple {
      color: $clr-purple;
    }
  }

  &__loading,
  &__error {
    text-align: center;
    font-size: 2rem;
    font-weight: 600;
    &-red {
      color: $clr-red;
    }
    &-blue {
      color: $clr-blue;
    }
    &-green {
      color: $clr-green;
    }
    &-purple {
      color: $clr-purple;
    }
  }

  &__table {
    overflow-x: auto;

    table {
      width: 90%;
      margin: 0 auto;
      border-collapse: collapse;

      th,
      td {
        padding: 0.5rem;
        text-align: center;
        border-bottom: 1px solid #ddd;
      }

      th {
        white-space: nowrap;
      }

      .table__header-commonName,
      .table__header-capital,
      .table__header-region,
      .table__header-population {
        cursor: pointer;
      }

      .icon-sort {
        font-size: 0.75rem;
      }

      .country-row {
        &__img {
          width: 100%;
          max-width: 100px;
        }

        &__link {
          color: black;
        }

        &__icon {
          font-size: 1.25rem;
          cursor: pointer;

          &-red {
            color: $clr-red;
          }
          &-blue {
            color: $clr-blue;
          }
          &-green {
            color: $clr-green;
          }
          &-purple {
            color: $clr-purple;
          }
        }
      }
    }
  }
}
